import React, { Component } from 'react';
import { Row } from 'simple-flexbox';
import './Footer.css'
import linkedInImg from '../../assets/linkedin.png'
import fbImg from '../../assets/facebook.png'

class Footer extends Component {

  constructor (props) {
    super(props);
    this.state = {
      linkedinUrl: 'https://linkedin.com/in/artptrapp',
      fbUrl: 'https://fb.com/artptrapp'
    };
  }

  handleClick(url) {
    window.open(url, '_blank')
  }

  render() {
    return (
      <div className="social-network">
        <Row vertical='center'>
        Fale comigo:
          <img className="social-icon" src={fbImg} alt='facebook' onClick={() => this.handleClick(this.state.fbUrl) } />
          <img className="social-icon" src={linkedInImg} alt='linkedin' onClick={() => this.handleClick(this.state.linkedinUrl) } />
        </Row>
      </div>
    );
  }
}

export default Footer;
