import React, { Component } from 'react';
import './TeamScore.css'

class TeamScore extends Component {

  constructor (props) {
    super(props);
    this.state = {
      startDate: new Date(2015, 7, 9),
      interval: this.props.interval,
      goals: 0
    };
  }

  componentDidMount() {
    setInterval(this.calculateGoals.bind(this), 1000);
  }

  calculateGoals () {
    if(this.state.interval === 0){
      this.setState({
        goals: 0
      })
      return;
    }
    let goals = Math.round(this.getMinutesBetweenDates(this.state.startDate, new Date()) / this.state.interval);
    this.setState({
      goals: goals
    })
  }

  getMinutesBetweenDates(startDate, endDate) {
    const diff = endDate.getTime() - startDate.getTime();
    return (diff / 60000);
  }


  render() {
    return (
      <p className="team-score">{this.state.goals}</p>
    );
  }
}

export default TeamScore;
